@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&family=Poppins:wght@600;700;800&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    scroll-behavior: smooth;
  }
  
  body {
    @apply bg-white text-dark font-sans;
  }

  h1, h2, h3, h4, h5, h6 {
    @apply font-heading font-bold;
  }
}

@layer components {
  .btn {
    @apply px-6 py-3 rounded-lg font-semibold transition-all duration-300;
  }

  .btn-primary {
    @apply bg-primary text-white hover:brightness-110;
  }

  .social-icon {
    @apply text-primary hover:animate-glow transition-all duration-300 text-2xl;
  }

  .nav-link {
    @apply hover:text-primary transition-colors duration-300;
  }
}

.crypto-card {
  @apply bg-white rounded-xl shadow-lg p-6 transition-transform duration-300 hover:-translate-y-2;
}
